import React, { useRef } from 'react';
import styled from 'styled-components';
import Container from '../components/Container';
import Countdown from '../components/Countdown';
import ScrollDownButton from '../components/ScrollDownButton';
import HollowText from '../components/HollowText';
import { colors } from '../constants/colors';
import { Cross } from '../components/Icons';
import { scrollTo } from '../utils/scrollTo'
import { nl2br, _w } from '../utils/wordingSystem';
import { respondTo } from '../utils/responsive';

const IntroSection = ({ ...props }) => {
  const wording = _w('intro');
  const rootRef = useRef(null);

  function handleClickScrollDownButton() {
    scrollTo(1, rootRef.current.offsetHeight + rootRef.current.offsetTop);
  }
  
  return (
    <Root ref={rootRef} {...props}>
      <Wrapper>
        <Heading>
          <div className="head">
            { wording.head.map((child) => 
              <div className="item" key={child}>
                <Cross className="icon" />
                <HollowText>{child}</HollowText>
              </div>
            )}
          </div>
          <HollowText className="title">{ nl2br(wording.title) }</HollowText>
          <BigTitle>{ wording.coming_soon }</BigTitle>
          <Countdown limit={wording.datetime} />
        </Heading>
        <div className="bottom">
          <ScrollDownButton onClick={handleClickScrollDownButton}>
            <div className="drop">{ wording.free }</div>
          </ScrollDownButton>
        </div>
        <img className="left" src="/images/charactor.png" alt="" />
        <img className="right" src="/images/charactor.png" alt="" />
      </Wrapper>
    </Root>
  )
}

const Root = styled.div`
`

const Wrapper = styled(Container)`
  position: relative;
  padding: 50px 0px 25px;
  ${respondTo.md} {
    padding-bottom: 140px;
  }
  > img {
    position: absolute;
    bottom: 0;
    width: 240px;
    height: auto;
    ${respondTo.md} {
      width: 136px;
    }
    &.left { left: 0; }
    &.right { right: 0; }
  }
  > .bottom {
    margin-top: 70px;
    text-align: center;
    ${respondTo.md} {
      margin-top: 35px;
    }
    .drop {
      color: ${colors.green};
      font-family: 'Nimbus-extend';
      font-size: 16px;
      ${respondTo.md} {
        font-size: 12px;
      }
    }
  }
`

const Heading = styled.div`
  color: ${colors.green};
  .head {
    display: flex;
    justify-content: center;
    margin-bottom: 22px;
    .item {
      display: flex;
      align-items: center;
      position: relative;
      margin-left: 12px;
      font-size: 28px;
      font-family: 'Nimbus-extend';
      ${respondTo.md} {
        font-size: 18px;
      }
      &:first-child {
        margin: 0;
        .icon { display: none; }
      }
      .icon {
        margin-right: 12px;
        color: ${colors.pink};
        ${respondTo.md} {
          margin-top: -4px;
        }
      }
    }
  }
  .title {
    font-size: 80px;
    line-height: 72px;
    font-family: 'Nimbus-extend';
    text-align: center;
    ${respondTo.md} {
      font-size: 32px;
      line-height: 32px;
    }
  }
  .description {
    font-size: 16px;
    strong {
      display: inline-block;
      margin: 0 8px;
      color: ${colors.pink};
    }
  }
`

const BigTitle = styled.div`
    margin-bottom: 35px;
    font-size: 80px;
    line-height: 72px;
    font-family: 'Nimbus-extend';
    text-align: center;
    ${respondTo.md} {
      margin-bottom: 24px;
      font-size: 32px;
      line-height: 32px;
    }
`

export default IntroSection;