import styled from 'styled-components';
import { colors } from '../../constants/colors';

const HollowText = ({color=colors.green, children, ...props}) => {
  return (
    <Root color={color} {...props}>
      { children }
    </Root>
  )
}

const Root = styled.div`
  text-shadow:  0px 0px 1px ${({color}) => color},
                0px 0px 1px ${({color}) => color},
                0px 0px 1px ${({color}) => color},
                0px 0px 1px ${({color}) => color},
                0px 0px 1px ${({color}) => color},
                0px 0px 1px ${({color}) => color},
                0px 0px 1px ${({color}) => color},
                0px 0px 1px ${({color}) => color},
                0px 0px 1px ${({color}) => color},
                0px 0px 1px ${({color}) => color};
  color: ${colors.black};
`

export default HollowText;