import { useEffect, useMemo, useRef, useState } from 'react';
import styled from 'styled-components';
import { colors } from '../../constants/colors';
import { respondTo } from '../../utils/responsive';

const Component = ({ limit }) => {
  const timerRef = useRef(null);
  const limitRef = useRef(null);
  const [ count, setCount ] = useState(null);
  const countRef = useRef(null);
  
  useEffect(() => {
    limitRef.current = new Date(limit).getTime();
    timerRef.current = setInterval(handleUpdateTime, 1000);
    return () => clearInterval(timerRef.current);
  }, [])

  function handleUpdateTime() {
    const now = new Date().getTime();
    setCount(limitRef.current - now)

  }
  
  return (
    <Root>
      { 
        count &&
        <>
          <Clock unit="days" count={count} />
          <Clock unit="hours" count={count} />
          <Clock unit="minutes" count={count} />
          <Clock unit="seconds" count={count} />
        </>
      }

    </Root>
  )
}

const Clock = ({ count, unit }) => {
  return (
    <ClockRoot>
      <div className="counter">
        { unit === 'days' && Math.floor(count / (1000 * 60 * 60 * 24)) }
        { unit === 'hours' && Math.floor((count % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)) }
        { unit === 'minutes' && Math.floor((count % (1000 * 60 * 60)) / (1000 * 60)) }
        { unit === 'seconds' && Math.floor((count % (1000 * 60)) / 1000) }
      </div>
      <div className="unit">{unit}</div>
    </ClockRoot>
  )
}

const Root = styled.div`
  display: flex;
  justify-content: center;
`

const ClockRoot = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 8px;
  border: 1px solid ${colors.green};
  border-radius: 50%;
  width: 100px;
  height: 100px;
  text-align: center;
  box-sizing: border-box; 
  ${respondTo.md} {
    width: 72px;
    height: 72px;
  }
  > .counter {
    margin-top: -12px;
    font-family: 'Oswald';
    font-size: 40px;
    color: ${colors.green};
    ${respondTo.md} {
      font-size: 24px;
    }
  }
  > .unit {
    text-transform: uppercase;
    text-align: center;
    font-family: 'Roboto Mono';
    font-size: 12px;
    letter-spacing: 0.5px;
  }
`

export default Component;