import { colors } from './colors'

function hexToRgbA(hex, alpha){
  var c;
  if(/^#([A-Fa-f0-9]{3}){1,2}$/.test(hex)){
      c= hex.substring(1).split('');
      if(c.length== 3){
          c= [c[0], c[0], c[1], c[1], c[2], c[2]];
      }
      c= '0x'+c.join('');
      return 'rgba('+[(c>>16)&255, (c>>8)&255, c&255].join(',')+',' + alpha + ')';
  }
  throw new Error('Bad Hex');
}

const patterns = {
  square:`background-color: transparent;
          background-image:  linear-gradient(${hexToRgbA(colors.green, 0.1)} 1px, transparent 1px), linear-gradient(to right, ${hexToRgbA(colors.green, 0.1)} 1px, transparent 1px);
          background-position: center center;
          background-size: 120px 120px;`,
          
  green: `background-color: transparent;
          background-image:  linear-gradient(${hexToRgbA(colors.green, 0.1)} 1px, transparent 1px), linear-gradient(to right, ${hexToRgbA(colors.green, 0.1)} 1px, transparent 1px);
          background-position: center center;
          background-size: 120px 120px;`,
          
  black: `background-color: transparent;
          background-image:  linear-gradient(${hexToRgbA(colors.black, 0.1)} 1px, transparent 1px), linear-gradient(to right, ${hexToRgbA(colors.black, 0.1)} 1px, transparent 1px);
          background-position: center center;
          background-size: 120px 120px;`,
}

export { patterns };